.clearfix:after {visibility: hidden; display: block; font-size: 0; content: ''; clear: both; height: 0; }

html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.sr-only { position: absolute; width: 1px; height: 1px; margin: -1px; border: 0; padding: 0; white-space: nowrap; clip-path: inset(100%); clip: rect(0 0 0 0); overflow: hidden;}

body {
  font-family: 'Montserrat', sans-serif;
  background-image: url("./assets/triangle-bg.png");
}

.App {
  text-align: center;
}

.wrapper {
  max-width: 1920px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

main {
  display: flex;
  justify-content: center;
  align-items: center;
}

h1, h2 {
  font-family: 'Balsamiq Sans', cursive;
  text-transform: lowercase;
}

h1 {
  font-size: 5.0rem;
  -webkit-text-stroke-color: #FFF;
  -webkit-text-stroke-width: 3px;
  margin: 0;
  text-shadow: 4px 4px rgba(0, 0, 0, 0.3);
}

h2 {
  font-size: 3.0rem;
  -webkit-text-stroke-color: #FFF;
  -webkit-text-stroke-width: 3px;
}

p {
  font-size: 1.0rem;
}

/* Canvas styles */

.canvas {
  background-color: #FFF;
  border-radius: 20px;
  border: 5px dashed #000;
  margin-top: 10px;
  touch-action: none;
  user-select: none;
}

.canvas-container {
  position: relative;
}

.canvas-header {
  margin-top: 6%;
}

.p-styles {
  background-color: #FFB33E;
  color: #000;
  border-radius: 10px;
  padding: 10px;
  display: inline-block;
}

.canvas-button-container {
  display: flex;
  justify-content: center;
  margin-bottom: 56px;
}

.palette-icon {
  font-size: 80px;
  margin-bottom: 10px;
}

/* Button styles */

.button-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 20px;
  margin-top: 20px;
}

.button-style {
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2rem;
  letter-spacing: 0.18rem;
  background-color: #FFF;
  border: 2px solid #000;
  padding: 10px 20px;
  text-transform: lowercase;
  margin-bottom: 10px;
  border-radius: 10px;
  
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-style:hover,
.button-style:focus {
  background-color: #FFB33E;
  transition: 0.5s ease-in-out;
}

a .button-style {
  margin-bottom: 0;
}

a {
  text-decoration: none;
}

.app-button {
  margin: 30px;
  display: inline-block;
}

i {
  color: black;
  font-size: 1.3rem;
  z-index: 5;
  margin-left: 10px;
}

.choose-color {
  position: relative;
  padding: 0;
  border-radius: 100%;
  height: 21px;
  width: 21px;
  border: none;
  outline: none;
  -webkit-apperance: none;
  vertical-align: middle;
  cursor: pointer;
  margin-left: 10px;
}

.choose-color::-moz-color-swatch {
  border: none;
  border-radius: 100%;
}

.choose-color::-webkit-color-swatch {
  border: none;
  border-radius: 100%;
}

.brush-slider {
  width: 120px;
}

/* Gallery styles */

.gallery-loader {
  border: 20px solid #f3f3f3;
  border-radius: 50%;
  border-top: 20px solid #FFB33E;
  border-right: 20px solid #2E3F73;
  border-bottom: 20px solid #F57C98;
  border-left: 20px solid #FFFFFF;
  width: 80px;
  height: 80px;
  margin: 20px auto;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin: 50px 0 70px 0;
}

.user-drawing img {
  width: 100%;
  background-color: #FFF;
  border: 2px solid #000;
  border-radius: 10px;
}

/* Footer styles */

footer {
  background-color: #FFB33E;
  position: relative;
  bottom: 0;
  width: 100%;
  padding: 1px 0;
}

footer p {
  font-size: 1.0rem;
}

footer i {
  font-size: 1.5rem;
}

/* Sweet Alert */

.swal-modal {
  border: 1px dashed black;
  width: 320px;
}

.swal-text {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

.swal-button {
  padding: 7px 19px;
  border-radius: 2px;
  background-color: #FFB33E;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8rem;
  color: #000;
  border: 1px solid #000;
  text-shadow: 0px -1px 0px rgba(0, 0, 0, 0.3);
}

.swal-button:not([disabled]):hover {
  background-color: #2E3F73;
  color: #FFF;
  transition: 0.5s ease-in-out;
}

.swal-footer {
  text-align: center;
}


/* Media Queries */

@media(max-width: 1024px) {

  .canvas-button-container {
    flex-direction: column-reverse;
    justify-content: center;
  }

  .palette-icon {
    display: none;
  }

  .button-container {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 20px 0 45px 0;
  }

  .button-style {
    width: 150px;
    height: 50px;
    margin: 5px;
  }

  .gallery-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media(max-width: 620px) {
  .gallery-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media(max-width: 600px) {
  h1 {
    font-size: 4.0rem;
  }
}

@media(max-width: 420px) {
  h1 {
    font-size: 3.2rem
  }
}

@media(max-width: 350px) {
  h1 {
    font-size: 3.0rem;
  }
  .app-button {
    display: flex;
    justify-content: center;
  }
}